*{
    box-sizing: border-box;
}

body{
    margin: 0;
}

.nav{
    display: flex;
    background: #1f1c1c;
    color: #fff;
    justify-content: space-between;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
    padding: 20px 30px;
    color: #dfc15e;
    position: fixed;
    width: 100%;
}

.logoNav{
    width: 85px;
    border-radius: 50%;
}

.nav a{
    color: inherit;
    text-decoration: none;
}

.nav a:hover{
    color: #ededed
}

.bar-bootcamp, .bar-inicio{
    font-size: 50px;
}

.nav ul{
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    padding: 0;
    margin: 0;
    list-style: none;
}

.nav li.active{
    color: #026c8a;
    font-weight: bold;
}

.nav li:hover{
    color: #ffffff;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.1s;

}
body{
  background-image: url(https://wallpaperaccess.com/full/1129027.jpg);
  background-position: center;
}

.descripcionInicioPadre{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 100px;
    padding-top: 200px;
    min-width: 100vw;
}
.descripcionInicio{
    display: flex;
    flex-direction: column;
    color: aliceblue;
    font-weight: bold;
    font-family:Georgia, 'Times New Roman', Times, serif;
    font-size: 25px;
    position: absolute;
    margin-top: 250px;
}

.descripcionInicio h1{
    display: flex;
    background-image: linear-gradient(to left, #0499c2, #071e54);
    height: 60px;
    align-items: center;
    margin-bottom: 45px;
    margin-left: 15px;
    box-shadow: 0 15px 15px #000000;
}

.descripcionInicio h2{
    margin-top: 25px;
    text-shadow: 4px 4px 8px #000000;
}

.imgInicioPadre{
    margin-right: 200px;
}

.imgInicio{
        width: 700px;
        border-radius: 10%;
        box-shadow: 0 15px 15px #000000;
        
}

.bar-bootcamp{
  margin-left: 20px;
}

/* SECCIÓN HISTORIA */

.seccionHistoria{
    background-color: #282c34;
    min-width: 100vw;
}

.historiaInicio{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    padding: 20px; 
    text-align: center;
    width: 800px;
    margin: auto;
}

.historiaInicio img{
    width: 150px;
    border-radius: 50%;
    box-shadow: 0 15px 15px #000000;
}

.historiaInicio p{
    color: aliceblue;
}

.historiaInicio h1, .historiaInicio h3{
    color: #dfc15e;
}


/* Mediaquerys */

@media screen and (max-width: 1200px){
    .descripcionInicio h1, .descripcionInicio h2{
      font-size: 32px;
      width: 550px;
    }
  }
  
  @media screen and (max-width: 990px){
    .imgInicio{
          width: 550px;
    }
  }
  @media screen and (max-width: 770px){
      .bar-bootcamp{
        display: none;
      }
      .logoNav{
        display: block;
    }
    .imgInicio{
        width: 380px;
  }

  .imgInicioPadre{
    margin-left: 200px;
}

    .descripcionInicio h1, .descripcionInicio h2{
      width: 350px;
      font-size: 25px;
    }
  }
  
  @media screen and (min-width: 770px){
    .bar-bootcamp{
      display: block;
    }
    .logoNav{
      display: none;
    }
  }
  
  /* Media Historia */
  
  
  @media screen and (max-width: 1200px){
  .historiaInicio p{
      font-size: 15px;
    }
  }
  
  @media screen and (max-width: 990px){
    .historiaInicio{
      width: 700px;
    }
  }
  @media screen and (max-width: 770px){
    .historiaInicio{
      width: 350px;
    }
  }
  

.listaDrinks{
    display: grid;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    padding: 20px;
    display: grid;
    grid-template-areas:
    'a b c'
    'd e f'
    'g h i';
    min-width: 100vw;
}

.carta{
    height: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 25px;
    background-image: url(https://wallpaperaccess.com/full/804379.jpg);
    background-repeat: no-repeat;
    background-position: cover;
    background-size: cover;
    padding-top: 100px;
    min-width: 100vw;
}

.cartaPadre h1{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: #dfc15e;
    text-shadow: 4px 4px 8px #000000;
    background: #3e3e3e;
    border-style: dotted;
    height: 50px;
    display: flex;
    align-items: center;
    
}

.presentacionCarta{
    display: flex;
    flex-direction: row;
}

.listaCarta{
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-shadow: 4px 4px 8px #000000;
    color: #c4c4c4;
    font-size: 30px;
}

.listaCarta h3{
    display: flex;
    align-items: center;
    background: #3d3d3d;
    border-style: dotted;
    height: 50px;
}
.bar-inicio{
    margin-left: 15px;
}
/* Querys */
    
    @media screen and (max-width: 990px){
        .cartaPadre h1{
            font-size: 20px;
        }
        .presentacionCarta{
            flex-direction: column;
        }
        .carta{
            height: 1100px;
        }
    }
    @media screen and (max-width: 770px){
        .cartaPadre h1, .listaCarta h3{
            font-size: 14px;
        }
    }

